<template>
    <div class="mc-wrap mc-info">
        <special-title title=""></special-title>
        <div class="currency-price">
            <span><span style="font-size:16px;margin-right:5px"></span>{{ price }}</span>
        </div>
    </div>
</template>

<script>
import SpecialTitle from './SpecialTitle'
export default {
    components: {
        SpecialTitle
    },
    props: {
        item: Object,
        code: Object
    },
    computed: {
        price() {
            if (!this.code.price) {
                return '()'
            }
            let price = this.code.price.toString()
            console.log(123, this.code.price)
            const dotIndex = price.indexOf('.')
            if (dotIndex == -1) {
                price = price + '.00'
            } else if (dotIndex === price.length - 2) {
                price = price + '0'
            }
            return price
        }
    }
}
</script>

