<template>
    <div class="fc-carousel">
        <el-carousel
            class="hoverStyle"
            v-if="imageLength"
            :height="height"
            style="background-color: #ffffff"
            arrow="never"
            trigger="click"
        >
            <el-carousel-item v-for="(src, idx) in item.value.images" :key="idx">
                <a :href="getCarouselImgUrl">
                    <img :style="{ width: '100%', height }" :src="imageSrc(src)" />
                </a>
            </el-carousel-item>
        </el-carousel>
        <div
            class="hoverStyle"
            v-else
            :style="{ height }"
            style="display: flex; justify-content: center; align-items: center; color: #888888; background-color: #ffffff"
        >
            <p>{{ '()' }}</p>
        </div>
        <div class="carousel-overlay" v-if="imageLength"></div>
    </div>
</template>

<script>
export default {
    props: {
        item: Object
    },
    data() {
        return {
            height: '210px'
        }
    },
    computed: {
        imageLength() {
            return this.item.value.images.length
        },
        // height() {

        //     return this.item.value.height || '200px'
        // },
        getCarouselImgUrl() {
            return this.item.value.url
        }
    },
    created() {
        const windowHeight = window.innerWidth || 375
        this.height = `${windowHeight / 375 * 210}px`
    },
    methods: {
        imageSrc(src) {
            return src + '?x-image-process=style/width-1024'
        }
    }
}
</script>

