<template>
    <div class="mc-wrap mc-info">
        <!-- <special-title title=""></special-title> -->
        <div class="public-title">
            <div></div>
            <img src="@/assets/images/bzq-icon.png" alt="" />
        </div>
        <div v-if="!shelfLife.beyondShelfLife" style="flex-grow: 2;">
            <div class="flex-sb">
                <div class="mc-info-gqsj">{{ expireDate }}</div>
                <div class="mc-info-dayNum" style="font-size: 12px;">
                    <span>{{ shelfLife.total }}</span
                    ><span class="mc-info-space"></span> <span>{{ shelfLife.remains }}</span
                    >
                </div>
            </div>
            <el-progress
                class="productInfo-progress"
                color="#3E71F7"
                :text-inside="true"
                define-back-color="#DCDEE0"
                :stroke-width="13"
                :percentage="shelfLife.percentage"
                text-color="#fff"
                stroke-linecap="square"
            ></el-progress>
        </div>
        <div v-else>
            <div style="font-size: 16px; color: #9A9A9A"></div>
        </div>
    </div>
</template>

<script>
import { format } from '@/utils/date-utils'
import SpecialTitle from './SpecialTitle'
export default {
    components: {
        SpecialTitle
    },
    props: {
        item: Object,
        code: Object,
        batch: Object
    },
    computed: {
        shelfLife() {
            const oneDay = 24 * 60 * 60 * 1000
            const startDate = new Date(this.batch.productionDate.substring(0, 10)) / oneDay
            const endDate = new Date(this.batch.beyondShelfLifeDate.substring(0, 10)) / oneDay
            const today = new Date(format(new Date(), 'yyyy-MM-dd')) / oneDay
            const res = {}
            res.total = today - startDate
            res.beyondShelfLife = today > endDate
            if (res.beyondShelfLife) {
                res.overTime = today - endDate
                res.percentage = 100
            } else {
                res.remains = endDate - today + 1
                if (today - startDate > 0) {
                    res.percentage = Math.round(((today - startDate) / (endDate - startDate)) * 100)
                } else {
                    res.percentage = 0
                    //res.total = 0
                }
            }
            return res
        },
        expireDate() {
            let date = new Date(this.batch.beyondShelfLifeDate.substring(0, 10))
            return format(date, 'yyyyMMdd')
        }
    }
}
</script>

