<template>
    <div class="home-msg-head flex-v-center">
        <el-image
            :src="musicIcon"
            :class="audio.playing == true ? 'mh-image' : 'move-image'"
            :lazy="true"
            @click="startPlayOrPause"
        ></el-image>
        <!-- <el-button type="text" @click="startPlayOrPause">{{ audio.playing | transPlayPause }}</el-button> -->
        <audio
            v-if="item.value.music != ''"
            :src="item.value.music"
            ref="audio"
            @pause="onPause"
            @play="onPlay"
            autoplay
            loop
        ></audio>
    </div>
</template>

<script>
import musicIcon from '@/assets/images/music.png'
import musicValue from '@/assets/images/1217325416042336256.mp3'

export default {
    props: {
        item: Object
    },
    data() {
        return {
            musicIcon,
            musicValue,
            audio: {
                //
                playing: false
            }
        }
    },
    methods: {
        asyncTitleInfo(item) {
            console.log(item)
            this.item = item
            this.fileList = []
            if (item.value.music != '') {
                this.fileList.push({ url: item.value.music, name: '' })
            }
        },
        //
        startPlayOrPause() {
            if (this.item.value.music != '') {
                return this.audio.playing ? this.pause() : this.play()
            }
        },
        //
        play() {
            this.$refs.audio.play()
        },
        //
        pause() {
            this.$refs.audio.pause()
        },
        //
        onPlay() {
            this.audio.playing = true
        },
        //
        onPause() {
            this.audio.playing = false
        }
    }
}
</script>

