.home-msg-head {
    height: 54px;
    justify-content: flex-start;
}
.mh-image {
    width: 25px;
    height: 25px;
    border-radius: 25px;
    border: 1px solid #e9e9e9;
    margin: 0 10px 0 18px;
}
.mh-text {
    font-size: 16px;
    color: #1d1c25;
}
.mh-text1 {
    margin: 0 auto;
    font-size: 16px;
